<template>
  <div id="app" class="container">
    <h1>Your Favourite Quotes Database</h1>
    <div class="row">
      <div class="col">
        <div class="progress">
          <div
            class="progress-bar"
            role="progressbar"
            aria-valuemin="0"
            aria-valuemax="100"
            :style="{ 'width': count * 10 + '%' }"
          >
            <span style="margin: 0 auto;">{{ count }} / 10</span>
          </div>
        </div>
      </div>
    </div>
    <br>
    <div class="row">
      <div class="col">
        <div class="form-group">
          <label for="quote">Paste a Quote:</label>
          <textarea
            v-model="inputQuote"
            class="form-control"
            id="quote"
            rows="5"
            style="width: 50%; margin: 0 auto;"
          ></textarea>
          <br>
          <button @click="addQuote" type="button" class="btn btn-primary">Add Quote</button>
          <br><br>
          <div
            v-if="warning"
            class="alert alert-warning"
            role="alert"
          >Too many quotes! Click on a quote to delete it.</div>
        </div>
      </div>
    </div>
    <div class="row">
      <div v-for="(quote, index) in quotes" :key="index" class="col-xs-5">
        <div @click="deleteQuote(index)" class="card" style="margin: 5px;">
          <div class="card-body">{{ quote }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "app",
  data() {
    return {
      quotes: [],
      inputQuote: "",
      warning: false,
      //count: 'width: ' + this.count * 10 + '%;'
      count: 0
    };
  },
  methods: {
    addQuote() {
      // Only add quote if less than 11 quotes exist
      // and input is not empty
      if (this.quotes.length < 10 && this.inputQuote != "") {
        this.quotes.push(this.inputQuote);

        // Update progress bar
        this.count++;
      }
      // Display warning whenever more than 10 quotes are displayed
      else if (this.quotes.length > 9) {
        this.warning = true;
      }
    },
    deleteQuote(index) {
      this.quotes.splice(index, 1);
      this.warning = false;
      this.count--;
    }
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
